.s-service-work {
    padding-bottom: 173px;

    @media (max-width: 1699px) {
        padding-bottom: 165px;
    }

    @media (max-width: 1199px) {
        padding-bottom: 76px;
    }

    @media (max-width: 991px) {
        padding-bottom: 75px;
    }

    @media (max-width: 600px) {
        padding-bottom: 50px;
    }

    &__wrapper {
        display: flex;

        @media (max-width: 600px) {
            max-width: 277px;
            flex-direction: column;
            margin: 0 auto;
        }
    }

    &__title {
        font-size: 19px;
        line-height: 120%;
        margin-right: 81px;
        white-space: nowrap;
        padding: 47px 0;

        @media (max-width: 1699px) {
            margin-right: 70px;
        }

        @media (max-width: 1199px) {
            font-size: 17px;
            margin-right: 32px;
            padding: 32px 0;
        }

        @media (max-width: 991px) {
            font-size: 15px;
            margin-right: 30px;
        }

        @media (max-width: 600px) {
            font-size: 14px;
            margin-bottom: 9px;
            padding: 0;
        }
    }

    &__items {
        max-width: 855px;
    }

    &__item {
        font-size: 21px;
        line-height: 120%;
        border: rem(2) solid;
        border-image-source: linear-gradient(
            124.81deg,
            #000000 -36.41%,
            #656669 117.15%
        );
        border-image-slice: 1;
        border-width: rem(2);
        padding: 47px 74px;
        margin-bottom: 26px;

        @media (max-width: 1699px) {
            padding-right: 54px;
            margin-bottom: 25px;
        }

        @media (max-width: 1199px) {
            font-size: 17px;
            padding: 32px 44px;
        }

        @media (max-width: 991px) {
            font-size: 15px;
            padding: 26px 56px;
        }

        @media (max-width: 600px) {
            font-size: 12px;
            margin-bottom: 15px;
            padding: 22px 34px;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }
}
