.s-service {
    position: relative;
    padding-top: 112px;
    padding-bottom: 101px;
    overflow: visible;

    @media (max-width: 1699px) {
        padding-top: 142px;
        padding-bottom: 85px;
    }

    @media (max-width: 1199px) {
        padding-top: 115px;
        padding-bottom: 40px;
    }

    @media (max-width: 991px) {
        padding-top: 50px;
        padding-bottom: 30px;
    }

    @media (max-width: 600px) {
        padding-top: 66px;
        padding-bottom: 25px;
    }

    &__title {
        font-size: 40px;
        margin-bottom: 21px;

        @media (max-width: 1699px) {
            font-size: 38px;
            margin-bottom: 20px;
        }

        @media (max-width: 1199px) {
            font-size: 35px;
            margin-bottom: 15px;
        }

        @media (max-width: 991px) {
            font-size: 25px;
        }

        @media (max-width: 600px) {
            font-size: 21px;
            margin-bottom: 11px;
        }
    }

    &__subtitle {
        font-size: 21px;

        @media (max-width: 1199px) {
            font-size: 19px;
        }

        @media (max-width: 991px) {
            font-size: 17px;
        }

        @media (max-width: 600px) {
            font-size: 14px;
        }
    }

    &__caption {
        position: absolute;
        top: -290px;
        right: -130px;
        font-size: 350px;
        font-weight: 900;
        letter-spacing: -0.03em;
        -webkit-text-fill-color: transparent;
        -webkit-text-stroke: 0.25rem rgba(255, 255, 255, 0.05);
        letter-spacing: -0.03em;

        @media (max-width: 1699px) {
            right: -450px;
        }

        @media (max-width: 1199px) {
            top: -250px;
            right: -340px;
            font-size: 230px;
        }

        @media (max-width: 991px) {
            top: -120px;
            right: -60px;
            font-size: 120px;
        }

        @media (max-width: 600px) {
            top: -150px;
            right: -70px;
        }
    }

    &__description {
        font-size: 15px;
        line-height: 120%;
        color: #4E5056;
        margin-top: 14px;

        @media (max-width: 1199px) {
            margin-top: 25px;
        }

        @media (max-width: 991px) {
            margin-top: 35px;
        }

        @media (max-width: 600px) {
            margin-top: 15px;
        }
    }
}
